import common from "../../common/common.js";

export default {
	data() {
		return {
			trailerList: [],
			keywords: "",		//搜索的关键字
			page: 1,			//当前第几页
			totalPages: 1		//总共多少页
		}
	},
	methods: {
		pagedTrailerList(keywords,page,pageSize){
			//查询猜你喜欢
			uni.showLoading({
				// 防止用户点击某一些按钮
				mask:true,
				title:"请稍后"
			})
			var serverUrl = common.serverUrl+ "/search/list?"+ common.qqStr+"&keywords="+keywords
										+"&page="+page
										+"&pageSize="+ pageSize;
			uni.request({
				url: serverUrl ,
				method: "POST",
				success: (res) => {
					if(res.data.status == 200){
						var tempList= res.data.data.rows;
						this.trailerList = this.trailerList.concat(tempList);
						this.totalPages = res.data.data.total;
						this.page = page;			//覆盖当前页面里的page
					}
				},
				complete:() => {
					uni.hideLoading();
				}
			});
		},
		searchMe(e){
			//var me = this;
			//获取搜索的内容
			var value = e.detail.value;
			this.keywords = value;
			this.trailerList = [];
			this.pagedTrailerList(value,1,15);
		},
		showTrailer(e){
			var trailerId = e.currentTarget.dataset.trailerid;
			uni.navigateTo({
				url: "../movie/movie?trailerId="+trailerId
			})
		}
	},
	onLoad() {
		this.pagedTrailerList(this.keywords,this.page,15)
		//查询猜你喜欢
		// uni.showLoading({
		// 	// 防止用户点击某一些按钮
		// 	mask:true,
		// 	title:"请稍后"
		// })
		// var serverUrl = common.serverUrl+ "/search/list?"+ common.qqStr+"&keywords=&page=&pageSize=";
		// uni.request({
		// 	url: serverUrl ,
		// 	method: "POST",
		// 	success: (res) => {
		// 		if(res.data.status == 200){
		// 			this.trailerList= res.data.data.rows;
		// 			console.log(this.trailerList)
		// 		}
		// 	},
		// 	complete:() => {
		// 		uni.hideLoading();
		// 	}
		// });
	},
	onReachBottom(){
		console.log("看到底裤了")
		var page = this.page + 1 ;//查询下一页面
		var keywords = this.keywords; //获得当前页面data的搜索值
		var totalPages = this.totalPages;
		if(page >= totalPages){
			console.log("没有页面了");
			return;
		}
		this.pagedTrailerList(keywords,page,15);
	}
}
